import React from "react";
import { ChatItem } from 'react-chat-elements';
import 'react-chat-elements/dist/main.css';
import './chatitem-overrides.css';

const MessageSingle = ({ user, content }) => {
    const statuses = ['green', 'red', 'orange'];
    const randomStatus = statuses[Math.floor(Math.random() * statuses.length)];
    return (
        <ChatItem
            avatar="https://www.svgrepo.com/show/452030/avatar-default.svg"
            alt="Reactjs"
            title={user.username || 'User'}
            subtitle={content || 'Message content'}
            date={new Date()}
            unread={0}
            statusColor={randomStatus}
            statusColorType="encircle"
        />
    );
};

export default MessageSingle;
