import React, { useEffect, useState } from "react";
import MessageSingle from "./MessageSingle";
import FriendCard from "./FriendCard";

const Messages = () => {
    const [messages, setMessages] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [friends, setFriends] = useState([]);
    const [friendLoading, setFriendLoading] = useState(true);
    const token = localStorage.getItem("accessToken") || "";
    const [currentConversation, setCurrentConversation] = useState([]);
    const [displayedMessages, setDisplayedMessages] = useState([]);

    // Fallback sample messages while there's no real data
    const sampleMessages = [
        { user: "User1", content: "Hello, how are you?" },
        { user: "User2", content: "I'm good, thanks! And you?" },
        { user: "User1", content: "Doing well, just working on a project." },
    ];

    // Fallback sample friends while there's no real data
    const sampleFriends = [
        { id: "1", username: "Friend1", avatar: null },
        { id: "2", username: "Friend2", avatar: null },
    ];

    console.log("Messages Component Mounted");

    const fetchAllConversations = async () => {
        setLoading(true);
        setError(null);

        console.log("Fetching messages with token:", token);
        try {
            const res = await fetch("/api/messages/conversations", {
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": token.startsWith("Bearer ") ? token : `Bearer ${token}`,
                },
            });
            if (!res.ok) {
                throw new Error(`Server responded with ${res.status}`);
            }
            const data = await res.json();

            console.log("Fetched Messages:", data);

            // Normalize response into a flat array of message objects { user, content, ... }
            let flat = [];
            if (Array.isArray(data)) {
                flat = data;
            } else if (data && typeof data === "object") {
                // If the backend returns a map of conversations, collect all messages found in values
                Object.values(data).forEach((val) => {
                    if (Array.isArray(val)) {
                        flat.push(...val);
                    } else if (val && typeof val === "object") {
                        // maybe each conversation contains a messages array at .messages
                        if (Array.isArray(val.messages)) {
                            flat.push(...val.messages);
                        } else {
                            // attempt to collect primitive entries
                            flat.push(val);
                        }
                    }
                });
            } else {
                flat = sampleMessages;
            }

            // Map/normalize messages to have { user, content } keys used by the UI
            const normalized = flat.map((m) => {
                if (typeof m === "string") return { user: "Unknown", content: m };
                return {
                    user: m.senderName || m.sender || m.user || m.from || "Unknown",
                    content: m.content || m.text || m.message || JSON.stringify(m),
                };
            });

            setMessages(normalized);
        } catch (err) {
            setError(err.message || "Failed to load messages");
            setMessages(sampleMessages);
        } finally {
            setLoading(false);
        }
    };

    const fetchFriends = async () => {
        setFriendLoading(true);
        try {
            const res = await fetch("/api/friends/list", {
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": token.startsWith("Bearer ") ? token : `Bearer ${token}`,
                },
            });
            if (!res.ok) {
                throw new Error(`Server responded with ${res.status}`);
            }
            const data = await res.json();
            console.log(data)
            setFriends(data)
        } catch (err) {
            console.error("Failed to fetch friends:", err);
            // preserve an empty list (or fallback sample)
            setFriends(sampleFriends);
        } finally {
            setFriendLoading(false);
            chooseConversation(friends[0].userName);
        }
    };

    const sendMessage = async (content) => {
        try {
            const res = await fetch("/api/messages/send", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": token.startsWith("Bearer ") ? token : `Bearer ${token}`,
                },
                body: JSON.stringify({ content }),
            });
            if (!res.ok) {
                throw new Error(`Server responded with ${res.status}`);
            }
            const data = await res.json();
            console.log("Message sent:", data);
            // Optionally refresh conversations after sending a message
            fetchAllConversations();
        } catch (err) {
            console.error("Failed to send message:", err);
        }
    };

    const chooseConversation = async (identifier) => {
        // identifier can be a friend's userName or userId
        console.log("Partner identifier:", identifier);

        // find friend object (if any) to obtain an id
        const friendObj = friends.find(f =>
            (f.userName && f.userName === identifier) ||
            (f.username && f.username === identifier) ||
            (f.userId && f.userId === identifier) ||
            (f.id && f.id === identifier)
        );

        // Search existing loaded messages first by sender/user name
        const convFromList = messages.filter(m => {
            const uname = ((m.user || m.senderName || (m.sender && (m.sender.userName || m.sender.username)) || "")).toString().toLowerCase();
            return identifier && uname.includes(identifier.toString().toLowerCase());
        });

        if (convFromList.length > 0) {
            setCurrentConversation(convFromList);
            setDisplayedMessages(convFromList);
            return;
        }

        // If not found locally, fetch conversation from backend when we have an id
        const partnerId = friendObj ? (friendObj.userId || friendObj.id || friendObj._id) : null;
        if (partnerId) {
            setLoading(true);
            setError(null);
            try {
                const res = await fetch(`/api/messages/conversation/${partnerId}?page=0&size=50`, {
                    headers: {
                        "Content-Type": "application/json",
                        "Authorization": token.startsWith("Bearer ") ? token : `Bearer ${token}`,
                    },
                });
                if (!res.ok) throw new Error(`Server responded with ${res.status}`);
                const data = await res.json();
                const content = Array.isArray(data) ? data : (data && Array.isArray(data.content) ? data.content : []);
                const normalized = content.map((m) => ({
                    user: m.senderName || (m.sender && (m.sender.userName || m.sender.username)) || m.user || "Unknown",
                    content: m.content || m.text || m.message || JSON.stringify(m),
                }));
                setCurrentConversation(normalized);
                setDisplayedMessages(normalized);
                return;
            } catch (err) {
                console.error("Failed to fetch conversation:", err);
                setError(err.message || "Failed to load conversation");
                setCurrentConversation([]);
                setDisplayedMessages([]);
            } finally {
                setLoading(false);
            }
        }

        // Fallback: nothing found
        setCurrentConversation([]);
        setDisplayedMessages(sampleMessages);
    };

    useEffect(() => {
        fetchAllConversations();
        fetchFriends();
    }, []);

    return (
        <div className="flex padding-10px">
            <div className="overflow-y-auto max-h-[400px] w-1/3">
                {friends.map((friend) => (
                    <FriendCard
                        key={friend.userId}
                        username={friend.userName}
                        avatar={friend.avatar}
                        onClick={() => chooseConversation(friend.userName)}
                    />
                ))}
            </div>
            <div className="w-px bg-gray-300 mx-2" />
            <div className="overflow-y-auto max-h-[400px] w-2/3">
                {displayedMessages.map((msg, index) => (
                    <MessageSingle key={index} user={msg.user} content={msg.content} />
                ))}
            </div>
        </div>
    );
};

export default Messages;